import React, { createContext, useContext, useEffect, useState } from 'react';
import * as Realm from 'realm-web';

const RealmContext = createContext<{ app?: Realm.App }>({});

interface RealmProviderProps {
  children: React.ReactNode;
}

export const RealmProvider = ({ children }: RealmProviderProps) => {
  const [app, setApp] = useState<Realm.App>();

  useEffect(() => {
    setApp(new Realm.App({ id: 'amsterdam-ussnf' }));
  }, []);

  return <RealmContext.Provider value={{ app }}>{children}</RealmContext.Provider>;
};

export const useRealm = () => useContext(RealmContext);
